<template>
  <user>
    <html-head 
      title="Users"
    ></html-head>

    <page-header>
      <template v-slot:title>All Users</template>
    </page-header>

    <template v-if="resources">
      <crud-table-card 
        :api="api" 
        :headers="headers" 
        :optionsChanged="optionsChanged" 
        :resources="resources" 
        name="user"
        :add="true"
        :edit="true"
        :trash="true"
        :view="true"
      ></crud-table-card>
    </template>
  </user>
</template>

<script>
import Api from './routes/api'
import Resources from '@/core/models/Resources'

export default {
  data: () => ({
    api: Api,
    headers: [
      {
        text: 'Image',
        align: 'start',
        value: 'image',
      },
      {
        text: 'First Name',
        align: 'start',
        value: 'firstName',
      },
      {
        text: 'Last Name',
        align: 'start',
        value: 'lastName',
      },
      {
        text: 'Role',
        align: 'start',
        value: 'roleDetails.name',
      },
      {
        text: 'Created',
        align: 'start',
        value: 'createdAt',
      },
      {
        text: 'Actions',
        align: 'end',
        value: 'actions',
      },
    ],

    resources: new Resources
  }),

  methods: {
    optionsChanged ( options ) {
      this.resources.optionsChanged(
        {
          method: 'get',
          url: Api.main(),
          params: {
            search: this.resources.search,
            searchFields: "firstName,lastName",
            options: this.resources.options
          }
        }, 
        options 
      )
    },
  },

  mounted () {
    this.resources.load( false )
  }
}
</script>